.modal {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  display: none;
  background: rgba(0, 0, 0, 0.5);

  &__content {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 250px;
  }

  &--active {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }

  &__input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
  }
}
